body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   overflow: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
:root {
  --primarycolor: #ffffff;
  --secondary: linear-gradient(
    to top,
    #060029,
    #101673,
    #13277d,
    #1a3686,
    #24448e,
    #1658a0,
    #046caf,
    #0080bd,
    #009fd3,
    #00bee2,
    #18ddec,
    #5ffbf1
  );
  background-color: #060029;
  --btn: linear-gradient(
    to bottom,
    #d307e0,
    #cb05d7,
    #c204ce,
    #ba02c6,
    #b201bd,
    #a501af,
    #9800a2,
    #8c0095,
    #780080,
    #65006c,
    #530059,
    #410046
  );
  --btn2: linear-gradient(
    to top,
    #ff001a,
    #f00016,
    #e10013,
    #d2000f,
    #c4000c,
    #b9000d,
    #ae000e,
    #a3000f,
    #980012,
    #8e0015,
    #830016,
    #780017
  );
  --btn3:linear-gradient(to top, #025e1b, #00731f, #008923, #00a026, #01b728);
}
