*{
padding: 0;
margin: 0;
box-sizing: border-box;

}


.container{
  position: relative;
  left:calc(50% - 250px);
  width: 30rem;
       
 }
figure{
    position: relative;
    top:30px;
    text-align: center;
}
figure h3{
font-family: Verdana, Geneva, Tahoma, sans-serif;
font-size: 15px;
font-weight: 600;

}
.ItemsList a{
  color: var(--primarycolor);
  
}
input[type=text]{
    padding: 10px;
    font-size: 15px;
    width: 80%;
    border: 2px solid rgb(255, 255, 255);
    background-color: rgb(255, 255, 255);
    border-radius: 20px;
    

}
input:focus{
  outline:none;
}

.addItem{
    position: relative;
    top: 50px;
   left:50px;
   
}

.ItemsList:hover{
  background-image:var(--btn);
}
.ItemsList{
   
    position: relative;
    top:80px;
    left: calc(25% - 80px);
    height: 50px;
    width: 80%;
    padding:10px;
    margin: 10px;
    font-size: 15px;
    border-radius: 10px;
    color:var(--primarycolor);
    background-image: var(--secondary);
}

.deletebtn{
    position: relative;
    float: right;
    bottom: 20px;
    font-size: 18px;
    
}
.Editbtn{
  position: relative;
  left:85%;
  bottom: 20px;
  font-size: 18px;
  
}
.deletebtn:hover{
    color:rgb(255, 0, 0);
    cursor: pointer;
  }
  .Editbtn:hover{
    color:rgb(0, 255, 0);
    cursor: pointer;
  }
  .btndiv{
    position: relative;
    left: calc(50% - 50px);
    top: 100px;
  }
  .btn{
    position:relative;
    background-image: var(--btn);
    color:var(--primarycolor);
    padding:15px;
    border:none;
    border-radius: 20px;

  }
  .btn:hover{
    background-image: var(--primarycolor);
    color: rgb(214, 58, 249);
    border:1px solid rgb(214, 58, 249) ;
  }
  .addbtn{
    background-image: var(--btn2);
    color:var(--primarycolor);
    padding:13px;
    border:none;
    font-weight: 400;
    border-radius: 20px;
  }
  .addbtn:hover{
    background-image: var(--primarycolor);
    color: rgb(252, 17, 0);
    border:1px solid rgb(248, 4, 4) ;
  }
  .addbtn{
    position: absolute;
    right:0;
    
  }
  .savebtn{
    background-image: var(--btn3);
    color:var(--primarycolor);
    padding:15px;
    border:none;
    font-weight: 500;
    border-radius: 20px;
  }
  .savebtn:hover{
    background-image: var(--primarycolor);
    color: #00841d;
    border:1px solid #00841d;
  }
  .savebtn{
    position: absolute;
    right:25px;
   
  }
  
 
  /*animated text css*/
  .wrapper{
    display:flex;
    position: relative;
    left:180px;
}
.wrapper .static-txt{
color:rgb(255, 255, 255);
font-size: 30px;
font-weight: 400;
}
.wrapper .dynamic-txts{
    margin-left:5px;
    height:35px;
    line-height: 32px;
    overflow: hidden;
}
.dynamic-txts li{
    list-style: none;
    color:rgb(245, 12, 12);
    font-size: 30px;
    font-weight: 500;
    position: relative;
    top:3.5px;
    animation: slide 6s steps(3) infinite;
}
@keyframes slide {
    100%{
        top: -95px;
    }
}
.dynamic-txts li span{
position: relative;
margin: 5px 0;
line-height: 25px;

}
.dynamic-txts li span::after{
content:"";
position: absolute;
left: 0;
height: 80%;
width:100%;
background-color:#060029;
border-left: 2px solid rgb(250, 3, 3);
animation: typing 2s steps(4) infinite;

}

@keyframes typing {
    40%,60%{
        left: calc(100% + 10px);

    }
    100%{
        left:0;
    }
}
  @media screen and (max-width: 360px){
.container{
  width:500px;
  left:calc(50% - 150px);
  
}
  }
  @media screen and (max-width: 480px){
    .container{
      width:400px;
      left:calc(50% - 250px);
      
    }
    .ItemsList{
      position: relative;
      left:70px;
    }
    .btn{
      position: relative;
      left:50px;
    }
    .addItem{
      position: relative;
      left:calc(50% - 160px);
      transform: scale(80%);
      }
      .addbtn{
        position: absolute;
        right:-10px;
      }
      figure{
        position: relative;
        top:30px;
        left:50px
    }
    .wrapper{
      display:flex;
      position: relative;
      left:140px;
  }
  .savebtn{
    position: relative;
    left:15px;
   
  }
  
    }
  @media screen and (max-width: 320px){

    .container {
      width:100px;
      left:50px;
     
    }
      }
    
